.leftbox {
	position: absolute;

	width: 200px;
}

.newPlan {
	all: unset;
	cursor: pointer;
	background-color: white;
	padding: 5px 0px;
	width: 150px;
	height: 40px;
	text-align: center;
	border-radius: 40px;
	box-shadow: 3px 3px 5px 1px rgba(150, 148, 150, 1);
	border: 2px solid #1f4d89;
	text-transform: uppercase;
	font-weight: 800;
	font-size: 12px;
	margin-top: 40px;
	margin-left: 35px;
	z-index: 1;
	transform: height 1s;
}

.newPlan:active {
	transform: scale(0.98);
	background-color: rgb(231, 225, 218);
	box-shadow: 2px 2px 5px 1px rgba(166, 164, 166, 1);
}
.newPlan:hover {
	height: 200px;
}
